import React from "react";
import { useDispatch, useSelector } from "react-redux";
import ReactPlayer from "react-player";
import styles from "./Video.module.scss";
import { playVideoActionCreator } from "../../redux/video-reducer";

const Video = () => {
  let state = useSelector((state) => state.player);
  const dispatch = useDispatch();
  let onPlayerClick = (currentTrack) => {
    dispatch(playVideoActionCreator(currentTrack));
  };
  let isPlaying = state.isPlaying;
  let currentVideo = state.url;
  let currentTitle = state.title;
  return (
    <div className={styles.videoPlayer}>
      {" "}
      {isPlaying ? (
        <div>
          <ReactPlayer
            url={currentVideo}
            controls={true}
            width="100%"
            height="100%"
            playing={true}
            changePlaybackRate={false}
            config={{ file: { attributes: { controlsList: "noplaybackrate nodownload" } } }}
          />
        </div>
      ) : (
        <div
          onClick={() => {
            onPlayerClick(currentVideo);
          }}
        >
          {currentTitle}
        </div>
      )}
    </div>
  );
};

export default Video;
